//  
import { SetUserInfo, RemoveUserInfo,LoginShow,LoginRemove } from "./constants.js";
// 
export const setUserInfo = (userInfo) => {
  return {
    type: SetUserInfo,
    userInfo,
  };
};
// 
export const removeUserInfo = () => ({
  type: RemoveUserInfo
});

//
export const loginShow = () => ({
  type: LoginShow
})

//
export const loginRemove = () => ({
  type: LoginRemove
})